@import url(https://fonts.googleapis.com/css?family=Quicksand|Work+Sans:700);
@import url(https://fonts.googleapis.com/css?family=Roboto|Staatliches);
body {
  background: #ecbeb4;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

i {
  font-size: 1.75em;
  color: #1e3d42;
}
#icon-link {
  margin: 0 .5em;
}

.Section {
  text-align: center;
  color: #1e3d42;
  margin: 0 10%;
  padding: 15px;
}
h1 {
  font-family: 'Staatliches', cursive;
  cursor: pointer;
}
p {
  font-family: 'Roboto', sans-serif;
}
a {
  color: #122528;
}
#main {
  background: #ecbeb4;
}
#main h1 {
  font-size: 5em;
}
#avocados {
  background: #519e8a;
}
#this {
  background: #c5c9a4;
}
#contact {
  background: #8bba7c;
}

